/* здесь подключите шрифты*/
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src:
    url("../../fonts/RobotoCondensed-Regular.woff2") format("woff2"),
    url("../../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src:
    url("../../fonts/RobotoCondensed-Bold.woff2") format("woff2"),
    url("../../fonts/RobotoCondensed-Bold.wofff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src:
    url("../../fonts/PTMono-Regular.woff2") format("woff2"),
    url("../../fonts/PTMono-Regular.woff") format("woff");
  font-display: swap;
}
